import * as THREE from 'three';


import vertex from '../shaders/vertex.glsl'
import fragment from '../shaders/fragment.glsl'



export default class{

constructor({scene,geometry,element,viewport,screen,material}){

this.scene = scene
this.geometry = geometry
this.element =  element
this.viewport = viewport
this.screen = screen
this.material = material



this.createMesh()
this.createTextures()    
this.createBounds()    
    


}

createMesh(){

this.material = new THREE.ShaderMaterial({ 
vertexShader : vertex,
fragmentShader: fragment,
uniforms:{
uTime :{value : 0},
tMap:{value : 0},
displacementX :{value: 1.5},
displacementY: {value: 2.0},
uResolution: {
type: 'v2',
value: new THREE.Vector2(this.screen.width, this.screen.height)
},
uViewportSizes: {
type: 'v2',
value: new THREE.Vector2(this.screen.width, -this.screen.height )
},
uImageResolution:{value:new THREE.Vector2(
this.element.getBoundingClientRect().width,
this.element.getBoundingClientRect().height
)},
uProgress:{value:0},
scale: {value: 0.2},

}
})
this.plane = new THREE.Mesh(this.geometry,this.material)
this.scene.add(this.plane)




}


createTextures(){

this.textures = new THREE.Texture(this.element)     
this.textures.needsUpdate = true;
this.material.uniforms.tMap.value = this.textures;


}

createBounds(){

this.bounds = this.element.getBoundingClientRect()
this.updateScale()
this.updateX()
this.updateY()

}


updateScale () {

this.plane.scale.x = this.viewport.width * this.bounds.width / this.screen.width
this.plane.scale.y = this.viewport.height * this.bounds.height / this.screen.height

}

updateX (x = 0) {
this.plane.position.x = -(this.viewport.width / 2) + (this.plane.scale.x / 2) + ((this.bounds.left - x) / this.screen.width) * this.viewport.width
}
  
updateY (y = 0) {
this.plane.position.y = (this.viewport.height / 2) - (this.plane.scale.y / 2) - ((this.bounds.top - y) / this.screen.height) * this.viewport.height
}


update (y) {
this.updateScale()
this.updateX()
this.updateY(y)
}

}