import LocomotiveScroll from 'locomotive-scroll';
import '../scss/locomotive-scroll.scss'

import * as THREE from 'three';


import Media from './media';

import {lerp} from './math'





class App{
constructor(){

this.time = 0
this.scroll = 0


this.scrolled = {
speed: 0,
previousSpeed : 0

} 


this.screen = {

width : window.innerWidth,
height : window.innerHeight

}




this.createScene()
this.createCamera()
this.createRenderer()
this.createGeometry()

this.onResize()
this.createSmoothScroll()


this.createMedias()


this.update()

this.addEventsListener()

}




 /**
  * locomotive scroll
  */
createSmoothScroll(){

this.locomotiveScroll = new LocomotiveScroll({

el:document.querySelector('.home'),
smooth:true,
multiplier : 1.4,
lerp:0.050,
reloadOnContextChange:false,
mobile: {
  smooth: true
},
tablet: {
  smooth: true
}
})


this.locomotiveScroll.on('scroll',(event)=>{
   
this.scroll = event.scroll.y
this.scrolled.speed = this.scrolled.previousSpeed - this.scroll;
this.scrolled.previousSpeed = this.scroll;

})





}


/**
 * three js scene
 */

createScene(){
this.scene = new THREE.Scene();
   
}

/**
 * three js camera
 */

createCamera(){
this.camera = new THREE.PerspectiveCamera( 75, this.screen.width / this.screen.height, 0.1, 1000 )
this.camera.position.z = 10
this.camera.fov = 45


}

/**
 * three js renderer
 */

createRenderer(){

this.renderer = new THREE.WebGLRenderer({alpha:true})
this.renderer.setPixelRatio(window.devicePixelRatio)
this.renderer.setSize(this.screen.width, this.screen.height)
document.body.appendChild(this.renderer.domElement)
    
    
}


/**
 * three js on resize function
 */

onResize(){



this.camera.aspect = window.innerWidth / window.innerHeight;
this.camera.updateProjectionMatrix();
this.renderer.setSize( window.innerWidth, window.innerHeight );

this.camera.updateProjectionMatrix()


const fov = this.camera.fov * (Math.PI / 180)

const height = 2 * Math.tan(fov / 2) * this.camera.position.z
const width = height * this.camera.aspect

this.viewport = {
    height,
    width
}


    
}




/**
 * media class
 */

createMedias(){


this.mediaElements = document.querySelectorAll('img')

this.medias = Array.from(this.mediaElements).map(element => {
    let media = new Media({
        element,
        scene: this.scene,
        geometry : this.geometry,
        viewport : this.viewport,
        screen : this.screen,
        material: this.material
    })
 
    return media
  })


}


/**
 * three js geometry
 */


createGeometry(){
this.geometry = new THREE.PlaneGeometry( 1,1, 10, 10 )
}
    
    
/**
 * global request animation frame 
 */

update() {

this.time += 0.04


if (this.medias) {

this.medias.forEach(media => {
media.update(this.scroll)
media.createBounds()
media.material.uniforms.uTime.value = this.time
media.material.uniforms.uProgress.value = lerp(media.material.uniforms.uProgress.value,this.scrolled.speed * 0.010,0.075) 
})


}

// this.onResize()

this.renderer.render( this.scene, this.camera )
window.requestAnimationFrame(this.update.bind(this))    

}


/**
 * window event listeners
 */

addEventsListener(){

window.addEventListener('resize',this.onResize.bind(this),false)

}

}

new App()


